class UICollection extends UIElement {
	constructor(params) {
		super(params);
	}

	append(ui, k) {
		if (k !== undefined) this[k] = ui;
		if (Array.isArray(ui.html)) {
			ui.html.forEach(el => {
				this.el.appendChild(el);
			});
		} else {
			this.el.appendChild(ui.el);
		}
	}

	remove(ui, k) {
		if (k) delete this[k];
		this.el.removeChild(ui.el);
	}

	removeK(k) {
		this.el.removeChild(this[k].el);
		delete this[k];
		console.log(this);
	}
}